<template>
<b-navbar toggleable="md" type="light" variant="light" :fixed="'top'">
  <div class="container-fluid">

    <b-nav-toggle target="nav_collapse"></b-nav-toggle>
    <b-navbar-brand to="/"><img src="../assets/navLogo.png" class="d-inline-block align-top" style="height:41px; padding:0;" /></b-navbar-brand>

    <b-collapse is-nav id="nav_collapse">
      <b-nav is-nav-bar class="ml-auto">
        <b-nav-item v-for="route in routes" :to="route.to">{{route.name}}</b-nav-item>
        <b-btn v-b-modal.myModal class="btn btn-warning my-2 my-md-0 ml-0 ml-md-2" role="button">Contact</b-btn>
      </b-nav>
    </b-collapse>
    <b-modal title="Click to Copy email Address" id="myModal" class='bg-light' header-bg-variant="dark" header-text-variant="light" body-bg-variant="dark" body-text-variant="light" footer-bg-variant="dark" footer-text-variant="light">
      <input id="contactInput" type="text" class="form-control rounded-0 rounded-left" placeholder="carterdanielr@gmail.com" v-model="copyData" :class="toggle == true ? 'selected' : null" />
      <button id="contactButton" class="btn btn-danger rounded-0 rounded-right" @click="select" v-clipboard="copyData" v-b-popover="'Copied!'"><i class="fa fa-clipboard mr-2" aria-hidden="true"></i>Copy</button>
    </b-modal>
  </div>
</b-navbar>
</template>
<script>
export default {
  data() {
    return {
      toggle: false,
      copyData: "carterknives@gmail.com",
      routes: [{
        to: '/about',
        name: 'story'
      }, {
        to: '/process',
        name: 'process'
      }, {
        to: '/knives',
        name: 'knives'
      }, {
        to: '/life',
        name: 'life'
      }, {
        to: '/map',
        name: 'local sellers'
      }]
    }
  },
  methods: {
    select(event) {
      targetId = event.currentTarget;
      console.log(targetId); // returns 'foo'
    }
  }
}
</script>
<style lang="scss">
.popover-body {
    color: inherit;
    font-weight: 500;
    padding: 8px 14px;
    margin-bottom: 0;
    font-size: 1rem;
    background-color: #f7f7f7;
    border-bottom: 1px solid #ebebeb;
    border-top-left-radius: calc(.3rem - 1px);
    border-top-right-radius: calc(.3rem - 1px);
}
.navbar {
    // box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    font-weight: bold;
    text-transform: capitalize;
    .btn {
        cursor: pointer;
        font-weight: bold;
        @media (min-width: 768px) {
            align-self: center;
        }
    }
}
.navbar-brand {
    padding: 0;
    margin-left: auto;
}
.modal-body {
    display: flex;
}
#contactInput {
    width: auto;
    flex: 1;
}
button.close {
    overflow: visible;
}
</style>



