<template>
<b-jumbotron :fluid="true" text-variant="white" :class="imageSrc.class">
  <img :src="imageSrc.logo" />
  <img class="bg desktop" v-parallax="0.2" :src="imageSrc.desktop" />
  <img class="bg mobile" :src="imageSrc.mobile" />
  <h1 class="display-3">{{imageSrc.title}}</h1>
</b-jumbotron>
</template>
<script>
export default {
  props: ['imageSrc']
}
</script>
<style lang="scss" scoped>
.jumbotron {
    height: 101vh;
    height: calc(101vh - 50px);
    display: flex;
    align-items: center;
    background: transparent;
    overflow: hidden;
    position: relative;
    h1 {
        text-transform: capitalize;
        text-align: center;
        z-index: 1;
        transform: translateY(-25px);
        @media (orientation:portrait) {
            transform: translateY(-50%);
        }
    }
    &.text-small {
        height: 110vh;
        @media (max-width:768px) {
            height: 100vh;
        }
        h1.display-3 {
            background: rgba(0,0,0,.25);
            padding: 2rem;
            min-width: 66%;
            margin: 0 auto;
            border: 5px solid white;
            font-size: 2rem;
            transform: none;
            @media (orientation:portrait) {
                transform: none;
            }
        }
    }
    &:before {
        position: absolute;
        content: "";
        z-index: 2;
        height: 15px;
        bottom: -15px;
        left: 0;
        width: 100%;
        -webkit-transform: scaleY(-1);
        transform: scaleY(-1);
        box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.035), 0 3px 7px -2px rgba(0, 0, 0, 0.068), 0 1px 12px 0 rgba(0, 0, 0, 0.07);
    }
    > div {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-flow: column wrap;
    }
    &.home {
        img:not(.bg) {
            z-index: 1;
            width: 66vmin;
            min-width: 320px;
            margin-bottom: 40%;
            @media (orientation:landscape) {
                margin-bottom: 0;
            }
        }
    }
    img.bg {
        position: absolute;
        height: 100%;
        width: 100%;
        object-fit: cover;
        object-position: 0 33%;
        left: 0;
        top: 0;
        &.desktop {
            @media (orientation:portrait) {
                display: none;
            }
        }
        &.mobile {
            display: none;
            object-position: left bottom;
            @media (orientation:portrait) {
                display: block;
            }
        }
    }
    &.knives {
        img.bg {
            object-position: 50% 50%;
            &.mobile {
                display: none;
                object-position: center top;
                @media (orientation:portrait) {
                    display: block;
                    object-position: center bottom;
                }
            }
        }
    }
    &.process {
        img.bg {
            object-position: 50% 50%;
            &.mobile {
                display: none;
                object-position: center top;
                @media (orientation:portrait) {
                    display: block;
                    object-position: center bottom;
                }
            }
        }
    }
    &.overlay {
        background: rgba(0,0,0,.25);
    }
    &.bus {
        img.bg {
            object-position: center bottom;
            &.mobile {
                object-position: center bottom;
            }
        }
    }
}
</style>



