<template>
<router-link tag="div" v-bind:to="card.path" v-b-modal="card.modal" class="card">
  <a>
    <img class="card-img-top" :src="imageSource" :alt="card.title">
    <h4 class="card-title">{{card.title}}</h4>
    <div class="card-body">
      <p class="card-text">{{card.content}}</p>
      <button class="btn btn-primary">{{card.button}}</button>
    </div>
  </a>
</router-link>
</template>
<script>
export default {
  props: ['card'],
  computed: {
    imageSource() {
      return this.card.image;
    }
  }
}
</script>



